import scoreStar from "../../components/scoreStar/scoreStar.vue"
export default {
	data() {
		return {
			searchList:{
				score:2
			},
			searchInfo:{},
			plotPicsArray:[],//剧照
			directorArray:[],//导演列表
			actorArray:[],//演员
			newStaffArray:[]
			
		}
	},
	
	//页面渲染完成之后,获得视频上下文对象
	onReady() {
		this.videoContext = uni.createVideoContext('myVideo');
	},
	
	onHide() {
		//页面被隐藏的时候,暂停播放
		this.videoContext.pause();
	},
	
	onShow() {
		//当页面再次显示的时候,可以继续播放
		if(this.videoContext){
			this.videoContext.play();
		}
		
	},
	
	onLoad(params) {
		var _this = this;
		//获取上个页面传入的参数
		var detailId = params.detailId;
		
		//根据api改变导航栏颜色
		uni.setNavigationBarColor({
			frontColor:"#FFFFFF",
			backgroundColor:"#000000"
		})
		
		//获取预告片的详情
		var serverUrl = _this.serverUrl;
		uni.request({
			url:serverUrl+'/search/trailer/'+detailId,
			method:'POST',
			success: (res) => {
				console.log(res);
				if(res.data.status == 200){
					_this.searchInfo = res.data.data;
					//把剧照的字符串转换为json  array
					_this.plotPicsArray = JSON.parse(_this.searchInfo.plotPics);
				}
			}
		});
		
		//获取导演列表
		uni.request({
			url:serverUrl+'/search/staff/'+detailId+'/1',
			method:'POST',
			success: (res) => {
				console.log(res);
				if(res.data.status == 200){
					
					_this.directorArray = res.data.data;
					
				}
			}
		});
		
		//获取演员列表
		uni.request({
			url:serverUrl+'/search/staff/'+detailId+'/2',
			method:'POST',
			success: (res) => {
				console.log(res);
				if(res.data.status == 200){
					_this.actorArray = res.data.data;
					
				}
			}
		})
	},
	
	//分享转发,只能在微信小程序端
	onShareAppMessage(res) {
		var _this = this;
		return {
			// title:_this.searchInfo.name,
			// path:'/page/movie/movie?id='+_this.searchInfo.id ,
			title:'分享啦~~~~',
			path:'/page/movie/movie' 
		}
	},
	
	
	//监听原生标题
	onNavigationBarButtonTap(e) {
		var index = e.index;
		console.log(index);
		var _this = this;
		var searchInfo = _this.searchInfo;
		var searchId = searchInfo.id;
		var searchName = searchInfo.name;
		var cover = searchInfo.cover;
		var poster = searchInfo.poster;
		//index为0为分享
		if(index == 0){
			uni.share({
				provider: "weixin",
				scene: "WXSceneTimeline",
				type: 0,
				title:"电影预告",
				href:"http://192.168.0.108:8080/#/pages/movie/movie",
				// href: "http://192.168.0.108:8080/#/pages/movie/movie?searchId="+searchId,
				summary: "我正在使用HBuilderX开发uni-app，赶紧跟我一起来体验！",
				imageUrl: "https://img-cdn-qiniu.dcloud.net.cn/uniapp/images/uni@2x.png",
				success: function (res) {
					console.log("success:" + JSON.stringify(res));
				},
				fail: function (err) {
					console.log("fail:" + JSON.stringify(err));
				}
			});
		}
	},
	
	
	
	methods: {
		
		//点击预览剧照
		lookMe(e){
			var _this = this;
			var index = e.currentTarget.dataset.index;
			console.log(index);
			uni.previewImage({
				current:_this.plotPicsArray[index],
				urls:_this.plotPicsArray
			})
		},
		
		//演职人员,实现数组拼接
		lookStaffs(e){
			var _this = this;
			var staffIndex = e.currentTarget.dataset.staffindex;
			//拼接导演和演员的数组,成为一个新的数组
			var directorArray = _this.directorArray;
			var actorArray = _this.actorArray;
			var newStaffArray = [];
			newStaffArray = newStaffArray.concat(directorArray).concat(actorArray);
		
			var urls = [];
			for(var i=0;i<newStaffArray.length;i++){
				var tempPhoto = newStaffArray[i].photo;
				urls.push(tempPhoto);
				console.log(urls);
			}
			
			uni.previewImage({
				current:urls[staffIndex],
				urls:urls
			})
		}
		
	},
	components:{
		scoreStar
	}
}
