export default {
	name:"scoreStar",
	data() {
		return {
			star:0,
			notstar:5
		}
	},
	props:{
		innerScore:0,
		showNum:0,
	},
	
	//生命周期,组件创建完成后被调用
	created() {
		var tempScore = 6;
		if(this.innerScore != null && this.innerScore != undefined && this.innerScore != ''){
			tempScore = this.innerScore;
		}
		
		var star = parseInt(tempScore/2);
		console.log('1111',star);
		var notstar = 5-star;
		
		this.star = star;
		this.notstar = notstar;
		
	},
	methods: {
		
	}
}
